
.form-controls {
  display: block;
  width: 90%;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  background-image: none;
  border: 1px solid #ccc;
}
.form-groups {
  margin-bottom: 15px;
  height: 2.3vw;
}
.input-groups {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  width: 100%;
  margin-top: -1px;
 
}
.glyphicon {
  right: 9px;
  position: relative;
  top: 1px;
  display: inline-block;
  font-family: 'Glyphicons Halflings';
  font-style: normal;
  font-weight: 400;
  line-height: 1;
}
.input-groups .form-controls, .input-group-addon, .input-group-btn {
  display: table-cell;
}
.panel-body{
  background: repeating-radial-gradient(#df8c8c24, #d5a5d41c 100px);

}
.CloseSign{
  color: red;
  font-size: 15px;
  font-weight: 1000;
  position: absolute;
  top: 7px;
  right: 13px;
}


