

#Profile {
    display: none;
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0px;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
  }

  .Profile-Popup-Content {
    background-color: #fefefe;
    margin: 2.5% auto;
    padding: 10px;
    border: 1px solid #888;
    width: 80%;
    max-width: 900px;
    /* top: 46px; */
    margin-top: 10rem;
    border-radius: 1rem;
    padding-right: 0rem;
  }

  label {
    margin-bottom: 0px;
    margin-top: 8px;
    font-weight: 500;
    color: black;
}

.Profile-HeadingText{
  margin-bottom: 0px;
  margin-top: 8px;
  font-weight: 500;
  color: black;
  text-align: center;
}

