.bg-footer {
    background-color: #18374f;
    /* padding: 20px 0 20px; */
    /* margin-bottom: 30px; */
}
.footer-heading {
    letter-spacing: 3px;
}

.footer-link a {
    color: #acacac;
    line-height: 25px;
    font-size: min(3vw,15px);
    transition: all 0.5s;
}
.list-inline{
    font-size: min(3vw,15px);
}


.footer-link a:hover {
    color: #1bbc9b;
}

.contact-info {
    color: #acacac;
    /* font-size: 14px; */
    font-size: min(3vw,15px);
}

.footer-social-icon {
    /* font-size: 5px; */
    height: 30px;
    width: auto;
    /* width:85%;
    height:auto;
    max-width:30px; */

    line-height: 20px;
    border-radius: 3px;
    text-align: center;
    display: inline-block;
}
.footer-social-icon:hover {
    color: #1bbc9b;
    cursor:pointer;
}

.facebook {
    /* background-color: #4e71a8; */
    color: #ffffff;
}

.twitter {
    /* background-color: #55acee; */
    color: #ffffff;
}

.google {
    /* background-color: #d6492f; */
    color: #ffffff;
}

.apple {
    /* background-color: #424041; */
    color: #ffffff;
}

.footer-alt {
    color: #acacac;
}

.footer-heading {
    position: relative;
    padding-bottom: 12px;
}

.footer-heading:after {
    content: '';
    width: 25px;
    border-bottom: 1px solid #FFF;
    position: absolute;
    left: 0;
    bottom: 0;
    display: block;
    border-bottom: 1px solid #1bbc9b;
}
.ParagraphOfFooter{
    color: #acacac;
}
