/* Add this CSS to your stylesheet or style block */
.hoverable-image {
  /* position: relative; */
  cursor: pointer;
}

.hoverable-image:hover::after {
  content: "Unblock user";
  position: absolute;
  background-color: #fff;
  /* Adjust the background color */
  color: #000;
  /* Adjust the text color */
  padding: 5px;
  /* Adjust the padding */
  border: 1px solid #ccc;
  /* Adjust the border */
  border-radius: 5px;
  /* Adjust the border radius */
  top: 100%;
  /* Position below the image */
  left: 50%;
  /* Center horizontally */
  transform: translateX(-50%);
  /* Center horizontally */
  z-index: 1;
  /* Ensure the tooltip appears above other elements */
}

