/* Toastify notification styles */
.Toastify__toast-container {
  position: fixed;
  z-index: 9999;
  top: 20px;
  right: 20px;
}

.Toastify__toast-body {
  font-size: 14px;
  padding: 10px;
}

.Toastify__toast--success {
  background-color: #28a745;
}

.Toastify__toast--error {
  background-color: #dc3545;
}



