  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  text-align: center;
  z-index: 10000;
  /* background-color: rgb(99, 95, 95, 0.5) */
}

/* Styling for the OK button */
#okButton {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
}



.popup {
  display: none;
  position: fixed;
  z-index: 9999;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.popup-content {
  background-color: #fefefe;
  margin: 2.5% auto;
  padding: 10px;
  border: 1px solid #888;
  width: 80%;
  max-width: 900px;
}

.close {
  color: #aaa;
  float: right;
  font-size: 30px;
  font-weight: bold;
  cursor: pointer;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.editinfo-container {
  max-width: 900px;
  width: 100%;
  padding: 20px 25px;
  border-radius: 10px;
  background: linear-gradient(135deg, #f9f9f9, #f0f7fe);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  height: 750px;
  overflow: auto;
}

#editinfo-container {
  &::-webkit-scrollbar-thumb {
    background: #888;
  }

