.collapsible {
  background-color: #777;
  background: linear-gradient(135deg, #dfdfdf, #baddfa);
  cursor: pointer;
  padding: 10px;
  width: 100%;
  border: none;
  text-align: left;
  outline: none;
  font-size: 15px;
  font-weight: 600;
  border-radius: 5px;
}

.collapsactive,
.collapsible:hover {
  background: linear-gradient(135deg, #c5c5c5, #acb8c1);
  border: 1px rgb(131, 133, 243) solid;
  border-radius: 5px;
}

.collapsible:after {
  content: '\002B';
  color: white;
  font-weight: bold;
  float: right;
  margin-left: 5px;
  border-radius: 5px;
}

.content,
.collapsactive:after {
  content: "\2212";
  border-bottom: 2px rgb(131, 133, 243) solid;
  border-radius: 5px;
}

.content {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s ease-out;
  background: linear-gradient(135deg, #f9f9f9, #e2effa);
  border-radius: 5px;
