#myForm input:invalid {
  border: 2px solid red;
  animation: shake forwards 0.3s ease;
}

#myForm input:valid {
  border: 2px solid green;
}

@keyframes shake {
  0% {
    transform: translateX(0);
  }

  25% {
    transform: translateX(2px);
  }

  50% {
    transform: translateX(-2px);
  }

  75% {
    transform: translateX(2px);
  }

  100% {
    transform: translateX(0);
  }
}

