/* UnderConstruction.css */
.underConstructionOfAgriculture {

  background-color: #f4f4f4;
  font-family: Arial, sans-serif;
}

.LoaderOfAgriculture {
  width: 100px;
  height: 120px;
  border: 16px solid #f3f3f3;
  border-top: 16px solid #a1db34;
  border-radius: 50%;
  animation: spin 0.7s linear infinite;
}

.SpinnerOfAgriculture {
  display: inline-block;
  vertical-align: middle;
}

.MainContentOfAgriculture {

  text-align: center;
  padding: 20px;
}





.LoadingSpinner {
  position: absolute;
  top: 600px;
  left: 874px;


}

.PAragraphOfApplication {
  color: rgba(250, 12, 12, 0.774);

}

.HeadingOfLoading {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'FontAwesome';
  margin-top: 80px !important;
  animation: bounce 2s infinite;
  color: rgb(71, 71, 4);
}


@keyframes bounce {

  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }

  40% {
    transform: translateY(-10px);
  }

  60% {
    transform: translateY(-5px);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

/* UnderConstruction.css */


.screen {
  opacity: 0.25;
  animation: slide 4s linear infinite;
}

.star {
  animation: bounce 4s linear infinite;
}

.shaddow {
  transform-origin: center center;
  animation: bounceShaddow 4s linear infinite;
}

@keyframes slide {
  from {
    transform: translatex(-10%);
  }

  to {
    transform: translatex(100%);
  }
}

@keyframes bounce {

  0%,
  40%,
  50%,
  60%,
  100% {
    transform: translatey(0%);
  }

  45%,
  55% {
    transform: translatey(-10%);
  }
}

@keyframes bounceShaddow {

  0%,
  40%,
  50%,
  60%,
  100% {
    opacity: 1;
    transform: scale(1, 1);
    color: #ff5733;
  }

  45%,
  55% {
    opacity: 0.3;
    transform: scale(0.8, 0.7) translateY(10%);
    color: #3498db;
  }
}


