.Card-Modal {
  background-color: #fff;
  padding: 5px;
  border: 3px solid;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  z-index: 10;
  border-radius: 14px;
  height: auto;
  width: 189px;
}
@media (max-width: 1760px){
  .Card-Modal {
    background-color: #fff;
    padding: 5px;
    border: 3px solid;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    z-index: 10;
    position: absolute;
    border-radius: 14px;
    margin-left: -250px;
    height: auto;
    width: 189px;
  }
}
@media (max-width: 768px) {
  .Card-Modal {
    width: 100%; /* Make the modal full width on small screens */
    height: auto; /* Allow the modal's height to adjust to content */
    margin: 0; /* Remove any margin */
  }
}

@media (max-width: 576px) {
  .Card-Modal {
    height: auto; /* Allow the modal's height to adjust to content */
    max-width: 90%; /* Limit the modal's width on extra small screens */
    margin: 0 auto; /* Center the modal horizontally */
  }

  .user-profile {
    flex-direction: column; /* Stack user profile vertically on extra small screens */
    text-align: center; /* Center user profile content */
  }

  .user-image img {
    margin: 0 auto; /* Center user image horizontally */
  }

  .user-details {
    margin-top: 10px; /* Add some space between user image and details */
  }

