.visually-hidden {
  opacity: 0;
}

.carousel {
  position: relative;
  top: -34px;
}

.carousel-indicators .active {
  height: 0;
}

.text-primary {
  font-family: 'open sans',sans-serif;
  color: #f9fbfd !important;
}
.carousel-indicators {
  bottom: 0px!important;
  display: none!important;
}

.text-info {
  color: #f4f7f7 !important;
  font-family: 'open sans',sans-serif;
}

/* Additional CSS for Text Animation */

/* Animate the color of the text */
@keyframes colorChange {
  from {
    color: #fff;
    /* Initial color */
  }

  to {
    color: #ffcc00;
    /* Target color */
  }
}

/* Animate the font size */
@keyframes fontSizeChange {
  from {
    font-size: 18px;
    /* Initial font size */
  }

  to {
    font-size: 24px;
    /* Target font size */
  }
}

/* Apply the animations to specific elements */

/* Apply colorChange animation to h3 elements */
.caption h3 {
  animation: colorChange 1s ease-in-out infinite alternate;
}

/* Apply fontSizeChange animation to p elements */
.caption p {
  animation: fontSizeChange 1s ease-in-out infinite alternate;
}

