.overlay {
  position: absolute;
  left: 150px;
  top: 10px;
  background-color: rgb(213, 235, 152);
  max-width: 200px;
  padding: 10px;
  box-sizing: border-box;
  height: 80px;
  z-index: 1000;
  color: #0b0b0b;
  overflow: hidden;
  white-space: normal;
  text-overflow: ellipsis;
}

/* admin */
.admin-item:hover+.sub-dropdown {
  display: block;
}

.sub-dropdown {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #f1f1f1;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.sub-dropdown ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.sub-dropdown li {
  padding: 10px;
  cursor: pointer;
}

.sub-dropdown li:hover {
  background-color: #ddd;
}

.drop {
  background-color: transparent;
  margin-top: -7px;
  font-size: 17px;
  transform-origin: top center;
  transition: all 300ms ease-in-out;
  position: fixed;
  top: 71px;
}

.dropdown-menulist {
  position: relative;
}

