/* YourComponent.css */

.containerOfForm {
  margin-top: 35px;

}

.formes-groupes {
  margin-bottom: 15px;
}

.ButtonForContactFormBox{
  
  max-height: 10px;
}

.labelcontact {
  font-weight: 500;
  color: black;
  font-size: min(3vw, 15px);
}

.formes-controles {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: min(3vw, 15px);
}

.textareaes {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.ButtonForContactForm {
  background-color: #0f3157;
  color: #fff;
  padding: 10px 25px;
  
  /* border-radius: 4px; */
  cursor: pointer;
  transition: background-color 0.3s;
  border: 1px solid white;
  font-weight: 700;

}

.ButtonForContactForm:hover {
  background-color: #0056b3;
  border-radius: 4px;
}

.FormBody {
  background: linear-gradient(to bottom, #007bff96, #0f3157);
  height: 696px;
  border: 1px solid #0037ff;
  /* padding-top: 20px; */
}


/* Center the form vertically and horizontally */
/* .row {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  } */
.TextOfMicronet {
  position: absolute;
  top: 428px;
  right: 128px;
  color: transparent;
  /* Make the text transparent */
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  /* Add a text shadow */
  background-image: linear-gradient(to right, #06081af2, #1a0ec7);
  /* Add a linear gradient background from left to right */
  -webkit-background-clip: text;
  /* Clip the background to the text */
  background-clip: text;
  /* font-weight: bolder; */
  font-size: 28px;
}

.AddressOfOffice {
  position: absolute;
  top: 553px;
  right: 231px;
  color: rgb(22, 2, 2);
  font-weight: bold;

}

.textContact {
  color: darkblue;
  /* Make the text transparent */
  /* text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5); */
  background-image: linear-gradient(to right, #0a1257f2, #17a2b8);
  /* Add a linear gradient background from left to right */
  -webkit-background-clip: text;
  /* Clip the background to the text */
  background-clip: text;
  font-weight: 500;
  font-family: 'FontAwesome';
}
@media (max-width: 768px) {
  .FormBody {
    height: auto;
    /* Allow the height to adjust based on content */
    padding-top: 20px;
    /* Add some top padding for smaller screens */
  }

  .formes-groupes {
    margin-bottom: 10px;
    /* Reduce margin for smaller screens */
  }

  .ButtonForContactForm {
    padding: 5px 15px;
    /* Smaller padding for buttons */
  }
}

.containerOfForm {
  color: goldenrod;
  font-family: 'Open Sans';
  margin-top: 30px;
}

.map-container {
  position: absolute;
  top: -29px;
}

.ImageOfMicronet {
  position: absolute;
  top: 494px;
  right: 201px;
}

.imageOfcontactsus {
  object-fit: cover;
  object-position: center;
  height: 348px;
  border: 1px solid #0037ff;
  filter: contrast(130%) brightness(110%);
}

.animated-slogan {
  text-align: center;
}

.slogan {
  margin-top: 20px;
  font-size: 28px;
  font-weight: bold;
  animation: float 3s ease infinite;
  background: linear-gradient(45deg, #e10b0b, #a31e1c, #601a0b, #b90f0f);
  background-size: 200% 200%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

@keyframes float {

  0%,
  100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-10px);
  }
}
