
 .modal {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); 
    justify-content: center;
    align-items: center;
    cursor: pointer;
  } 
  
 .modal.open {
    display: flex;
  }
  
  .AuModal {
    background-color: #fff;
    padding: 20px;
    border-radius: 8px;
    position: relative;
    width: 400px; 
  }
  
  .close {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 20px;
    cursor: pointer;
  }
  
  
    .AuModal-Btn {
    background-color: #007bff;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  } 
  
  .AuModal-Btn:hover {
    background-color: #0056b3;
  }  
  
  .SaveBtn{
