.custom-checkbox {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  width: 16px;
  height: 16px;
  outline: none;
  /* margin-right: 5px; */
  /* vertical-align: middle; */
  cursor: pointer;
  position: relative;
}

.custom-checkbox::before {
  content: "";
  position: absolute;
  width: 14px;
  height: 14px;
  border: 2px solid #4caf50;
  border-radius: 3px;
  /* background-color: white; */
  box-sizing: border-box;
  /* top: -1px; */
  /* left: -1px; */
}

.custom-checkbox:checked::before {
  background-color: #4caf50;
  /* Set the background color when checked */
  background-image: url("/public/whiteTick.webp");
  /* Replace with your checkmark image path */
  background-size: 100% 100%;
  color: white;
}
